.footer {
    margin-top: 4rem;
    background-color: var(--color-bg-variant);
    opacity: 0.8;
    width: 100%;
    height: 55vh;

    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;

    position: relative;
}

.footer a {
    color: var(--color-white);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;

}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    position: absolute;
    bottom: 0;
    right: 0;

    font-style: italic;
    opacity: 0.5;
}

/* ================== MEDIA QUERIES ( MEDIEUM DEVICES ) ==================== */

@media screen and (max-width : 724px) {
    .footer {
        height: 35vh;
        margin-top: 3rem;
        height: 60% !important;
    }

}

/* ================== MEDIA QUERIES ( SMALL DEVICES ) ==================== */

@media screen and (max-width : 600px) {
    .footer {
        height: 45vh;
        margin-top: 2rem;
    }

    .permalinks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 1.0rem;
        margin: 0 auto 3rem;
    }
}